@import "../../../scss/variables.scss";

/* Category */
.category {
  & .category__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 36px;
    row-gap: 12px;
    list-style: none;
    margin: 0;
    margin-left: 20px;
    padding: 0;
  }

  & .category__item {
    position: relative;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
    color: $gray;
  }
}

.car {
  margin-top: 48px;

  & .car-list {
    display: flex;
    flex-direction: column;
    font-weight: 400;

    & .car-list__item {
      position: relative;
      border: 1px solid $gray-light;
      cursor: pointer;
      height: 180px;
      padding: 16px;

      &:hover,
      &:focus {
        border-color: $main-accent;
      }

      & .car-name {
        font-size: 18px;
        line-height: 21px;
        text-transform: uppercase;
        color: $black;
      }

      & .car-price {
        margin-top: 5px;
        font-size: 14px;
        line-height: 16px;
        color: $gray;
      }

      & .car-image {
        position: absolute;
        width: auto;
        height: 65%;
        bottom: 16px;
        right: 16px;
        box-sizing: content-box;
        z-index: -1;
      }
    }

    @media (min-width: $tablet-width) {
      & .car-list__item {
        height: 150px;
        & .car-image {
          position: absolute;
          width: auto;
          height: 65%;
        }
      }
    }

    @media (min-width: $desktop-min-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;

      & .car-list__item {
        & .car-image {
          width: auto;
          height: 45%;
        }
      }
    }

    @media (min-width: $desktop-width) {
      & .car-list__item {
        max-height: 224px;
        & .car-image {
          width: auto;
          height: 65%;
        }
      }
    }
  }
}
